.homepage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1.5rem;
}

.homepage .content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.homepage .text-zone .my-name{
    text-transform: uppercase;
    color: var(--alternativecolor);
    margin-bottom: 1rem;
}

.homepage .text-zone .title{
    color: var(--maincolor);
    font-weight: bold;
}

.homepage .text-zone .title h1{
    font-size: 3.5rem;
    margin-bottom: 1rem;
}

.homepage .text-zone .title p{
    font-size: 1.3rem;
    color: var(--alternativecolor);
}

.home-menu {
    font-weight: bold;
    position: absolute;
    bottom: 2rem;
    left: 1.5rem;
}

.home-menu .link-separator{
    color: var(--alternativecolor);
    margin-left: 1rem;
    margin-right: 1rem;
}

.homepage .illustration-zone{
    width: 50%;
    display: flex;
    justify-content: center;
    padding-left: 100px;
}

.homepage .illustration-zone .illustration-img{
    min-width: 380px;
    width: 380px;
    min-height: 380px;
    height: 380px;
    background: var(--maincolor);
    border-radius: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.homepage .illustration-zone .illustration-img img{
    width: 80%;
    height: 80%;
}

.homepage .illustration-zone .deco-circle{
    width: 60px;
    height: 60px;
    border: 30px solid var(--maincolor);
    border-radius: 50%;
    position: absolute;
    left: -60px;
}


.homepage .illustration-zone .deco-circle:nth-child(1){
    border: 30px solid var(--maincolor);
    top: 80px;
    animation: circle-1 0.7s ease-in-out alternate-reverse infinite;
    animation-delay: 0.4s;
}

.homepage .illustration-zone .deco-circle:nth-child(2){
    border: 5px solid var(--secondarycolor);
    width: 40px;
    height: 40px;
    left: -80px;
    top: calc(50% - 20px);
    animation: circle-2 0.8s ease-in-out alternate-reverse infinite;
    animation-delay: 0.2s;
}

.homepage .illustration-zone .deco-circle:nth-child(3){
    border: 15px solid var(--maincolor);
    bottom: 80px;
    animation: circle-1 0.7s ease-in-out alternate-reverse infinite;
    animation-delay: 0.1s;
}

@keyframes circle-1 {
    from{
        border-width: 30px;
        left: -60px;
    }
    to {
        border-width: 5px;
        left: -80px;
    }
}

@keyframes circle-2 {
    from{
        border-width: 5px;
        left: -80px;
    }
    to {
        border-width: 20px;
        left: -60px;
    }
}

@media all and (max-width: 1024px){

    .homepage .illustration-zone .illustration-img{
        min-width: 250px;
        min-height: 250px;
        width: 250px;
        height: 250px;
        margin-top: 1rem;
    }

    .homepage .illustration-zone .deco-circle{
        display: none;
    }

}

@media all and (max-width: 600px){

    .homepage .illustration-zone .illustration-img{
        min-width: 250px;
        min-height: 250px;
        width: 250px;
        height: 250px;
        margin-top: 1rem;
    }

    .homepage .illustration-zone {
        justify-content: initial;
        padding-left: 0;
    }

}

@media all and (max-width: 490px){

    .homepage .illustration-zone .illustration-img{
        min-width: 200px;
        min-height: 200px;
        width: 200px;
        height: 200px;
        margin-top: 1rem;
    }


    .homepage .text-zone .my-name{
        text-transform: uppercase;
        color: var(--alternativecolor);
        margin-bottom: 0rem;
    }

    .homepage .illustration-zone {
        justify-content: initial;
        padding-left: 0;
    }

    .homepage .text-zone .title h1{
        font-size: 2.5rem;
    }

    .homepage .text-zone .title p{
        font-size: 1rem;
        font-weight: 300;
    }

}

@media all and (max-width: 330px){

    .homepage .illustration-zone .illustration-img{
        min-width: 160px;
        min-height: 160px;
        width: 160px;
        height: 160px;
        margin-top: 1rem;
    }

    .homepage .illustration-zone {
        justify-content: initial;
        padding-left: 0;
    }

    .homepage .text-zone .title h1{
        font-size: 2rem;
    }

    .homepage .text-zone .title p{
        font-size: 1rem;
        font-weight: 300;
    }

}
