/* ==========================================================================
   mobile Navigation
   ========================================================================== */
header{
    position: relative;
}

#logo{
    transition: all 0.3s ease-in-out;
}

.active-menu #logo .color-primary{
    fill: rgba(255, 255, 255, 0.8);
    stroke: rgba(255, 255, 255, 0.8);
}
.active-menu #logo .color-secondary{
    display: none;
}

#logo circle{
    fill: none!important;
}

#logo-brand{
    overflow: hidden;
    position: fixed;
    top: 1.5rem;
    left: 1.5rem;
    display: flex;
    z-index: 2;
}

#logo-brand #logo{
    height: 40px;
    width: auto;
}

.logo-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 10px;
}

.logo-text .logo-title{
    font-size: 11px;
    letter-spacing: 0.30em;
    color: var(--alternativecolor);
    transition: color 0.3s;
}

.logo-text .logo-name{
    font-size: 13px;
    color: var(--maincolor);
    text-transform: uppercase;
    transition: color 0.3s;
}

.active-menu .logo-title{
    color: rgba(255, 255, 255, 0.8);
}

.active-menu .logo-name{
    color: rgba(255, 255, 255, 0.8);
}

/**menu**/
.menu-toggler{
    cursor: pointer;
    position: fixed;
    top: 1.5rem;
    right: 1.5rem;
    display: flex;
    align-items: center;
    color: var(--alternativecolor);
    transition: 0.3s;
    text-transform: uppercase;
    font-size: 0.8rem;
    font-weight: bold;
    z-index: 2;
}

.active-menu .menu-toggler{
    color: rgba(255, 255, 255, 0.8);
}

.toggler-btn {
    display: block;
    width: 25px;
    height: 25px;
    position: relative;
    text-align: center;
    line-height: 40px;
    transform: translate3d(0, 0, 0);
    transition: all ease-out 200ms;
    cursor: pointer;
    z-index: 2;
    margin-left: 10px;
}

.active-menu .hamburger {
    background: #fff;
}

.menu-toggler:hover, .menu-toggler:hover .hamburger{
    color: var(--maincolor);
}

.menu-toggler:hover .hamburger{
    background: var(--maincolor);
}

.active-menu .menu-toggler:hover, .menu-toggler:hover .hamburger{
    color: var(--alternativecolor);
}

.active-menu .menu-toggler:hover .hamburger{
    background: var(--alternativecolor);
}

.hamburger {
    width: 25px;
    height: 3px;
    background: var(--alternativecolor);
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -12.5px;
    margin-top: -1.5px;
    transition: all 200ms;
}

.hamburger-1 {
    transform: translate3d(0, -8px, 0);
}

.hamburger-2 {
    transform: translate3d(0, 0, 0);
}

.hamburger-3 {
    transform: translate3d(0, 8px, 0);
}

.active-menu .hamburger-1 {
    transform: translate3d(0, 0, 0) rotate(45deg);
}

.active-menu .hamburger-2 {
    transform: translate3d(0, 0, 0) scale(0.1, 1);
}

.active-menu .hamburger-3 {
    transform: translate3d(0, 0, 0) rotate(-45deg);
}


