.homepage .illustration-zone{
    width: 50%;
    display: flex;
    justify-content: center;
    padding-left: 100px;
}

.homepage .illustration-zone .illustration-img{
    min-width: 380px;
    width: 380px;
    min-height: 380px;
    height: 380px;
    background: var(--maincolor);
    border-radius: 50%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.homepage .illustration-zone .illustration-img img{
    width: 80%;
    height: 80%;
}

.homepage .illustration-zone .deco-circle{
    width: 60px;
    height: 60px;
    border: 30px solid var(--maincolor);
    border-radius: 50%;
    position: absolute;
    left: -60px;
}


.homepage .illustration-zone .deco-circle:nth-child(1){
    border: 30px solid var(--maincolor);
    top: 80px;
    animation: circle-1 0.7s ease-in-out alternate-reverse infinite;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

.homepage .illustration-zone .deco-circle:nth-child(2){
    border: 5px solid var(--secondarycolor);
    width: 40px;
    height: 40px;
    left: -80px;
    top: calc(50% - 20px);
    animation: circle-2 0.8s ease-in-out alternate-reverse infinite;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.homepage .illustration-zone .deco-circle:nth-child(3){
    border: 15px solid var(--maincolor);
    bottom: 80px;
    animation: circle-1 0.7s ease-in-out alternate-reverse infinite;
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}

@-webkit-keyframes circle-1 {
    from{
        border-width: 30px;
        left: -60px;
    }
    to {
        border-width: 5px;
        left: -80px;
    }
}

@keyframes circle-1 {
    from{
        border-width: 30px;
        left: -60px;
    }
    to {
        border-width: 5px;
        left: -80px;
    }
}

@-webkit-keyframes circle-2 {
    from{
        border-width: 5px;
        left: -80px;
    }
    to {
        border-width: 20px;
        left: -60px;
    }
}

@keyframes circle-2 {
    from{
        border-width: 5px;
        left: -80px;
    }
    to {
        border-width: 20px;
        left: -60px;
    }
}

