    margin: 0.7rem 0;
}

.form {
    width: 100%;
    min-width: 500px;
    margin-bottom: 2rem;
}

.form h3{
    color: var(--alternativecolor);
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.form form{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.form form label{
    width: 45%;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    margin: 2rem .5rem 2rem 0;
}

.form form label > *{
    font-size: 15px!important;
}

.form form .full-border {
    padding: 0.5rem 0;
    border: 1px solid rgba(135, 138, 143, 0.3);
}

.form ::-webkit-input-placeholder{
    color: var(--maincolor);
}

.form :-ms-input-placeholder{
    color: var(--maincolor);
}

.form ::-ms-input-placeholder{
    color: var(--maincolor);
}

.form ::placeholder{
    color: var(--maincolor);
}

.form .single-bar{
    padding: 0.5rem 0;
    border-bottom: 1px solid rgba(135, 138, 143, 0.3);
}

.form .message{
    width: 100%;
}

.form .message textarea{
    overflow:hidden;
    padding:10px 0;
    width:100%;
    display:block;
    border-bottom:1px solid rgba(135, 138, 143, 0.3);
    transition: all 0.3s;
    height: auto;
}

.form form .label-title{
    color: var(--alternativecolor);
    margin-bottom: .3rem;
}

.btn-container {
    width: 100%;
    margin-top: 1rem;
}

@-webkit-keyframes topAnimation {
    from {
        transform: translate(0rem, 0);
    }
    to {
        transform: translate(0rem, 3.5rem);
    }
}
@keyframes topAnimation {
    from {
        transform: translate(0rem, 0);
    }
    to {
        transform: translate(0rem, 3.5rem);
    }
}
@-webkit-keyframes bottomAnimation {
    from {
        transform: translate(-11.5rem, 0);
    }
    to {
        transform: translate(0rem, 0);
    }
}
@keyframes bottomAnimation {
    from {
        transform: translate(-11.5rem, 0);
    }
    to {
        transform: translate(0rem, 0);
    }
}

button.btn {
    text-transform: uppercase;
    text-decoration: none;
    font-weight: 700;
    border: 0;
    position: relative;
    letter-spacing: 0.15em;
    margin: 0 auto;
    padding: 1rem 2.5rem;
    background: transparent;
    outline: none;
    font-size: 13px;
    color: var(--maincolor);
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.15s;
    cursor: pointer;
}

button.btn::after, button.btn::before {
    border: 0;
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    z-index: -2;
    border-radius: 50%;
}

button.btn::before {
    border: 0;
    background-color: var(--secondarycolor);
    top: -0.75rem;
    left: 0.5rem;
    -webkit-animation: topAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.25s infinite alternate;
    animation: topAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.25s infinite alternate;
}

button.btn::after {
    background-color: var(--maincolor);
    top: 3rem;
    left: 13rem;
    -webkit-animation: bottomAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.5s infinite alternate;
    animation: bottomAnimation 2s cubic-bezier(0.68, -0.55, 0.265, 1.55) 0.5s infinite alternate;
}

button.btn:hover {
    color: white;
}

button.btn:hover::before, button.btn:hover::after {
    top: 0;
    height: 100%;
    width: 100%;
    border-radius: 0;
    -webkit-animation: none;
    animation: none;
}

button.btn:hover::after {
    left: 0rem;
}

button.btn:hover::before {
    top: 0.5rem;
    left: 0.35rem;
}

@media all and (max-width: 930px){
    .contact {
        flex-direction: column;
        padding: 5rem 1.5rem 1.5rem;
    }

    .title {
        font-size: 2rem;
        margin-top: 0.5rem;
    }

    .form h3 {
        font-size: 1.2rem;
    }

    .form {
        width: 100%;
        min-width: 100%;
    }

    .form form label {
        width: 45%;
        display: flex;
        flex-direction: column;
        font-size: 20px;
        margin: 1rem .5rem 1rem 0;
    }

    .contact {
        padding: 4rem 1.5rem 1.5rem;
        display: flex;
        justify-content: flex-start;
    }
}


@media all and (max-width: 420px){
    .contact {
        flex-direction: column;
        padding: 5rem 1.5rem 1.5rem;
    }


    .form form label:nth-child(3), .form form label:nth-child(4) {
        width: 100%;
    }

    .page-title{
        margin-bottom: 1rem;
        margin-right: 0;
        min-width: 100%;
        max-width: 100%;
    }

    .title {
        font-size: 1.5rem;
        margin-top: 0.5rem;
    }

    .subtitle {
        font-size: 12px;
    }
    .form h3 {
        font-size: 1rem;
        font-weight: 200 !important;
    }

}

